@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,800;0,900;1,400;1,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --black: #fff;
  --white: #333;
}

.dark{
  --black: #fff;
  --white: #333;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 40px 100px;
  z-index: 99999;
}

header .logo {
  position: relative;
  display: inline-flex;
  color: var(--black);
  text-decoration: none;
  font-size: 2em;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.rightSide {
  display: flex;
}
.cubslogo {
  max-height: 45px;
}
.btns {
  position: relative;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-left: 10px;
}

.btns.menuToggle ion-icon{
  font-size: 3em;
}

.btns ion-icon {
  font-size: 1.5em;
  color: var(--black);
}

.btns ion-icon:nth-child(2) {
  display: none;
}

.btns.active ion-icon:nth-child(2) {
  display: block;
}

.btns.active ion-icon:nth-child(1) {
  display: none;
}

.navigation{
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--white);
  z-index: 5;
  transition: 0.5s;
}
.navigation.active{
 left: 0;
}

.navigation li {
  list-style: none;
}
.navigation li a{
  display: inline-flex;
  margin: 5px 0;
  font-size: 1.35em;
  text-decoration: none;
  color: var(--black);
  padding: 5px 20px;
  border-radius: 40px;
}
.navigation li a:hover{
  background: var(--black);
  color: var(--white);
}
.copyrightText{
  position: absolute;
  left: 100px;
  bottom: 40px;
  z-index: 4;
  font-weight: 500;
  font-size: 1.2em;
  color: var(--black);
}
.sci{
  position: absolute;
  right: 100px;
  bottom: 30px;
  z-index: 4;
  display: flex;
  list-style: none;
  flex-direction: column;
}

.sci li a{
  text-decoration: none;
  color: var(--black);
  font-size: 1.75em;
}
@media(max-width:768px) {
  header {
    padding: 20px;
  }

  .copyrightText  {
    font-size: 1em;
    left: 25px;
    bottom: 10px;
  }

  .sci {
    left: 20px;
    bottom: 30px;
  }
  @media (max-width: 768px) {
    .sci {
      display: none;
    }
  }
}
@keyframes titleTransition {
  from{
    opacity: 0.25 ;
  }
 to{
   opacity: 1;
 }
}

@keyframes maskTransition {
  0%{
    opacity: 1;
  }
 50%{
   opacity: 0.5;
 }
 100%{
   opacity: 1;
 }
}

.main {
  overflow: hidden;
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.mask{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  mix-blend-mode: screen;
  animation: mask 8s infinite ease-in-out;
}

.mask{
  -webkit-filter: invert(1);
          filter: invert(1);
  mix-blend-mode: multiply;
}

img.mask {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}

.main h2 {
  color: #fff;
  position: relative;
  z-index: 3;
  font-size: 16vw;
  text-shadow: 0 20px 30px rgba(0, 0, 0, 0.2);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

video {
  /*animation: 2.5s ease-in-out titleTransition;*/
}

.titleTrans{
  animation: 1s linear titleTransition;
}

.loading{
  position: absolute;
  color: var(--black);
  height: 100%;
  width: 100%;
  background: var(--white);
  z-index: 4;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 22px;
}

.formCotainer {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--white);
}

.formWrapper{
  max-width: 80vw;
  min-width: 40vw;
  height: 100vh;
  transition: all 2s ease-in-out;
  display: flex;
  flex-direction: column;
  padding: 40px 20px;
  transform: scaleX(1);
}
.formIframe{
  width: 80%;
}
@media (max-width: 1024px) {
  .formWrapper{
    max-width: 80vw;
    min-width: 30vw;
    height: 100%;
  }
  .formCotainer h1,  .formWrapper p{
    font-size: 3em;
  }
  .formWrapper h1,   .formWrapper p{
    font-size: 3em;
  }
}
@media (max-width: 768px) {
  .formWrapper{
    margin-top: 30px;
    max-width: 100vw;
    min-width: 100vw;
    height: 100vh;
  }
  .formWrapper h1, .formWrapper p{
    display: none;
  }
}
@media (max-width: 376px) {
  input#firstname{
    margin-bottom: 16px;
  }
  input#lastname{
    margin-top: 8px;
  }
}

.formItemWrapper{
  display: flex;
  flex-wrap: wrap;
  margin: 8px 0px;
}

.formItem{
  flex: 1 1;
  display: flex;
  flex-direction: column;
}
.formItem, h1, .formItem p{
  margin: 0 5px;
}

.formItem input{
  font-weight: 500;
  padding: 5px;
  border-radius: 5px;
  color: black;
  border: 1px solid #ccc;
}
.formItem label{
  color: var(--black);
}
.formWrapper h1, .formItem p{
  color: var(--black);
}

.formItem button {
  width: 100px;
  padding: 8px;
  align-self: flex-end;
  border-radius: 5px;
  border: 1px solid #ccc;
}
.formItem textarea {
 border: 1px solid #ccc;
 border-radius: 5px;
}


.about-container .conteudo {
  color: #777;
  background-color: var(--white);
  text-align: justify;
  padding: 5vh 5vw;
}

.about-container .caixa1, .caixa2, .caixa3, .caixa4 {
  transition: all 5s ease-in-out;
  position: relative;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.8;
}

.about-container p {
  margin-bottom: 20px;
}

.about-container h3 {
  text-transform: uppercase;
  color: var(--black);
  text-align: center;
  padding-bottom: 10px;
}

/*efeito parallax*/
.about-container .conteudo-titulo {
  display: flex;
  align-items: center;
  width: 100%;
  height: 500px;
}

.conteudo-titulo span.titulo {
  background-color: var(--black);
  padding: 18px;
  font-weight: 600;
  font-size: 4em;
  letter-spacing: 5px;
  color: var(--white);
  text-transform: uppercase;
  margin: 0 auto;
}
